<template>
<div class="gmap-cmp">
  <GmapMap class="map"
     ref="mapRef"
    :center="{ lat: 32.584139, lng: 35.005980 }"
    :zoom="10"
    map-type-id="terrain"
  >
    <GmapMarker
      :key="index"
      v-for="(m, index) in markers"
      :position="m.pos"
      :clickable="true"
      :draggable="true"
      @click="center = m.position"
    />
  </GmapMap>
  <section class="storesPan">
  <button @click="panToStore(markers[0].pos)">Haris</button>
  <button @click="panToStore(markers[1].pos)">Jerusalem</button>
  <button @click="panToStore(markers[2].pos)">Ashdod</button>
  </section>
  </div>
</template>

<script>
export default {
  name: "Google-Maps",
  data() {
    return {
      markers: [
        { pos: { lat: 32.462448, lng: 35.043359 } },
        { pos: { lat: 31.773418, lng: 35.227228} },
        { pos: { lat: 31.791804, lng: 34.654925 } },
      ],
    };
  },
  methods:{
    panToStore(pos){
        this.$refs.mapRef.$mapPromise
        .then((map) => {
          map.panTo(pos)
        })
    }
  }
};
</script>

<style></style>
