<template>
  <section class="toy-preview col-layout">
    <img class="toy-image" :src=toy.image >
    <h3 class="toy-name">{{ toy.name }}</h3>
    <h5>Price: ${{ toy.price }}</h5>
    <h5>Type: {{ toy.type }}</h5>
    <h5 :style="stockColor" class="toy-stock">{{getInStock}}</h5>
    <section class="actions flex space-between">
      <i class="fas fa-trash " @click="onDelete"></i>
      <i class="fas fa-info-circle " @click="showDetails"></i>
      <i class="fas fa-edit " @click="onEdit"></i>
    </section>
  </section>
</template>

<script>
export default {
  name: "toy-preview",
  props: ["toy"],
  computed:{
    stockColor(){
      return this.toy.inStock? {"color":"#17a058"} : {"color":"red"}
    },
    getInStock(){
       return this.toy.inStock? "In Stock!":"Out Of Stock.."
    }
  },
  methods: {
    onDelete() {
      this.$emit("delete", this.toy._id);
    },
    onEdit() {
      this.$router.push(`/edit/${this.toy._id}`);
    },
    showDetails() {
      this.$router.push(`/toy/${this.toy._id}`);
    },
  },
};
</script>


