<template>
  <section class="accordion-item"  @click="openItem">
    <header class="item-header">
      <h1>{{ title }}</h1>
      <i class="fas fa-arrow-circle-down" :class="{clicked:this.itemClicked}"></i>
    </header>
    <slot v-if="itemClicked"></slot>
  </section>
</template>

<script>
export default {
  name: "accordion-item",
  props: ["title"],
  data() {
    return {
      itemClicked: false,
    };
  },
  methods: {
    openItem() {
      this.$parent.toggleAccordion();
      this.itemClicked = !this.itemClicked;
    },
  },
};
</script>

