<template>
  <div class="toy-details main-layout" v-if="toy">
    <header class="details-header">
      <h1 class="cmp-title">Toy Details:</h1>
      <i class="fas fa-arrow-alt-circle-left" @click="goBack"></i>
    </header>
    <section class="details flex" v-if="toy">
      <section class="toyInfo">
      <h2><span>Toy Id:</span> {{ toy._id }}</h2>
      <h2><span>Name:</span> {{ toy.name }}</h2>
      <h3><span>Price:</span> ${{ toy.price }}</h3>
      <h3><span>Toy Type:</span> {{ toy.type }}</h3>
      <h3><span>Added:</span> {{addedTime }}</h3>
      <h3 :style="stockColor" class="toy-stock"><span>{{getInStock}}</span></h3>
      <p>
        Give your children a gift that will be cherished. At the time of build
        up, children can discuss how to make excellent works, cultivate
        children's logical thinking and Hands-on ability. DIY is always a great
        thing for kids, they will get much happier when finished making an
        awesome item by themselves. It is a companion partner for children.
      </p>
      </section>
      <img class="toy-image" :src=toy.image >
    </section>
    <section class="reviews">
      <h1>Reviews</h1>
    </section>
  </div>
  <h3 v-else>Loading Toy...</h3>
</template>

<script>
import { toyService } from "../services/toy-service.js";

export default {
  name: "toy-details",
  data() {
    return {
      toy: null,
    };
  },
  created() {
    let id = this.$route.params.id;
    toyService.getById(id).then((toy) => (this.toy = toy));
  },
  computed:{
      stockColor(){
      return this.toy.inStock? {"color":"#17a058"} : {"color":"red"}
    },
    getInStock(){
       return this.toy.inStock? "In Stock!":"Out Of Stock.."
    },
    addedTime(){
      return new Date(this.toy.createdAt).toLocaleDateString()
    }
  },
  methods: {
    goBack() {
      this.$router.push("/");
    },
  },
};
</script>
