<template>
  <div class="pop-up">
    <i
      class="fab fa-rocketchat fa-3x"
      @click="popIsOpen = true"
      v-if="!popIsOpen"
    ></i>
    <chat-app v-else>
      <h3 slot="chat-header" class="chat-title">Chat Bot:</h3>
      <li slot="messages"  v-for="(message, idx) in messages" :key="idx" :class="{botMsg:message.sender==='Bot'}">
        <span>{{message.sender}}</span>: {{ message.txt }}
      </li>
      <section slot="chat-footer" class="user-panel flex space-around">
        <input
          type="text"
          placeholder="Speak With Us.."
          v-model="userMsg"
          @keyup.enter="addMessage"
        />
        <button @click="addMessage">Send</button>
      </section>
    </chat-app>
  </div>
</template>

<script>
import chatApp from "./chat-app.cmp.vue";
export default {
  props:["open"],
  name: "pop-up",
  data() {
    return {
      popIsOpen:this.popIsOpen,
      userMsg: "",
      messages: [{sender:'Bot',txt:"Hello! How Can i Help You?",style:''}],
    };
  },
  methods: {
    addMessage(){
      if(this.userMsg==='') return
      this.messages.push({sender:'You',txt:this.userMsg})
      this.userMsg=''
      setTimeout(()=>this.messages.push({sender:'Bot',txt:"Sure Thing Honey"}),500)
    }
  },
   created() {
    document.addEventListener("keyup", (ev) => {
      if (ev.key === 'Escape') this.popIsOpen = false;
    });
  },
  destroyed() {
    document.removeEventListener("keyup",  ()=>this.popIsOpen = false);
  },
  components: {
    chatApp,
  },
};
</script>

<style></style>
